  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 50px;
  text-shadow: 2px 2px white;
  -webkit-animation: pulse 10s infinite;
          animation: pulse 10s infinite;
  margin-bottom: 40px; }

.start-button:hover {
  display: inline-block;
  color: white;
  -webkit-animation: hover-ripple linear .7s, push-in .7s 1, text-fade forwards 3s;
          animation: hover-ripple linear .7s, push-in .7s 1, text-fade forwards 3s; }

@-webkit-keyframes text-fade {
  0% {
    text-shadow: 0 0 2px 2px black; }
  100% {
    text-shadow: 4px 4px #04485D; } }

@keyframes text-fade {
  0% {
    text-shadow: 0 0 2px 2px black; }
  100% {
    text-shadow: 4px 4px #04485D; } }

@-webkit-keyframes push-in {
  50% {
    -webkit-transform: scale(0.95);
            transform: scale(0.95); } }

@keyframes push-in {
  50% {
    -webkit-transform: scale(0.95);
            transform: scale(0.95); } }

@-webkit-keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 #B5DAE1; }
  25% {
    box-shadow: 0 0 20px 10px #f7edeb; }
  50% {
    box-shadow: 0 0 40px 20px #f7edeb; }
  100% {
    box-shadow: 0 0 0 0 #04485D; } }

@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 #B5DAE1; }
  25% {
    box-shadow: 0 0 20px 10px #f7edeb; }
  50% {
    box-shadow: 0 0 40px 20px #f7edeb; }
  100% {
    box-shadow: 0 0 0 0 #04485D; } }

@-webkit-keyframes hover-ripple {
  0% {
    box-shadow: 0 0 0 0 #B5DAE1; }
  50% {
    box-shadow: 0px 0px 10px 10px white; }
  100% {
    box-shadow: 0px 0px 0px 0px transparent; } }

@keyframes hover-ripple {
  0% {
    box-shadow: 0 0 0 0 #B5DAE1; }
  50% {
    box-shadow: 0px 0px 10px 10px white; }
  100% {
    box-shadow: 0px 0px 0px 0px transparent; } }

body {
  overflow: hidden; }

.bubbles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  -webkit-transform: translateZ(0);
          transform: translateZ(0); }

@-webkit-keyframes bubble {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  100% {
    -webkit-transform: translateY(-1200px);
            transform: translateY(-1200px); } }

@keyframes bubble {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  100% {
    -webkit-transform: translateY(-1200px);
            transform: translateY(-1200px); } }

.bubbles li {
  border-radius: 50%;
  position: absolute;
  list-style: none;
  display: block;
  background-color: #91D0D7;
  opacity: .2;
  border: 8px solid #046679;
  bottom: -100px;
  -webkit-animation: bubble 30s infinite;
          animation: bubble 30s infinite;
  -webkit-transition-timing-function: linear;
          transition-timing-function: linear; }

.bubbles li:nth-child(1) {
  width: 35px;
  height: 35px;
  left: 20%; }

.bubbles li:nth-child(2) {
  width: 60px;
  height: 60px;
  left: 40%;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
  -webkit-animation-duration: 26s;
          animation-duration: 26s; }

.bubbles li:nth-child(3) {
  width: 80px;
  height: 80px;
  left: 60%;
  -webkit-animation-delay: 5s;
          animation-delay: 5s; }

.bubbles li:nth-child(4) {
  width: 50px;
  height: 50px;
  left: 67%;
  -webkit-animation-duration: 24s;
          animation-duration: 24s; }

.bubbles li:nth-child(5) {
  width: 40px;
  height: 40px;
  left: 82%;
  -webkit-animation-delay: 14s;
          animation-delay: 14s;
  -webkit-animation-duration: 25s;
          animation-duration: 25s; }

.bubbles li:nth-child(6) {
  width: 20px;
  height: 20px;
  left: 87%;
  -webkit-animation-delay: 8s;
          animation-delay: 8s;
  -webkit-animation-duration: 28s;
          animation-duration: 28s; }

.bubbles li:nth-child(7) {
  width: 70px;
  height: 70px;
  left: 10%;
  -webkit-animation-delay: 12s;
          animation-delay: 12s;
  -webkit-animation-duration: 28s;
          animation-duration: 28s; }

.bubbles li:nth-child(8) {
  width: 45px;
  height: 45px;
  left: 30%;
  -webkit-animation-delay: 6s;
          animation-delay: 6s; }

.bubbles li:nth-child(9) {
  width: 16px;
  height: 16px;
  left: 5%;
  -webkit-animation-delay: 7s;
          animation-delay: 7s; }

.bubbles li:nth-child(10) {
  width: 22px;
  height: 22px;
  left: 55%;
  -webkit-animation-delay: 4s;
          animation-delay: 4s; }

.message-container {
